import productcarddetails_snippet from '@/components/productcarddetails_snippet.vue'

export default {
    components: {
        productcarddetails_snippet,
    },
    mounted() {
        this.getLatestProducts();
        this.getBestsellingProducts();
        this.getTopratedProducts();
    },
    data() {
        return {
            latestProducts:[],
            bestsellingProducts:[],
            topratedProducts:[],
        };
    },
    methods: {
        getLatestProducts(){
            var _this = this;
            this.$axios.$get('/api/getLatestProducts')
            .then(function (response) {
                _this.latestProducts = response.data;
                _this.latestProducts = _this.arrayOfObjectSortDesc(_this.latestProducts,  'sellingPrice')
            })
            .catch(function (error) {
            })
        },

        getBestsellingProducts(){
            var _this = this;
            this.$axios.$get('/api/getBestsellingProducts')
            .then(function (response) {
                _this.bestsellingProducts = response.data;
                _this.bestsellingProducts = _this.arrayOfObjectSortDesc(_this.bestsellingProducts,  'sellingPrice')
            })
            .catch(function (error) {
            })
        },


        getTopratedProducts(){
            var _this = this;
            this.$axios.$get('/api/getTopratedProducts')
            .then(function (response) {
                _this.topratedProducts = response.data;
                _this.topratedProducts = _this.arrayOfObjectSortDesc(_this.topratedProducts,  'sellingPrice')
            })
            .catch(function (error) {
            })
        },



    },
    computed: {


    }
};
